<template>
  <div :class="containerStyle" style="width: 100%;">
    <div class="mr-4" :style="accentStyle">
      <div class="d-flex flex-row align-center">
        <div
          :style="`width: ${accentSize}px; height: ${accentSize}px; border-width: ${borderSize}px;`"
          class="action-icon-holder d-flex align-center justify-center"
          :key="accentSize"
        >
          <v-icon :style="iconSize">{{icon}}</v-icon>
        </div>
        <div class="primary" :style="`width: ${barWidth}px; height: ${borderSize}px;`"></div>
        <div class="primary" :style="`width: ${borderSize}px; height: ${accentSize}px;`"></div>
      </div>
    </div>
    <span :class="changeDescClasses" style="line-height: 1.15;">
      <span v-if="this.$vuetify.breakpoint.mdAndUp">{{title}}</span>
      <br v-if="this.$vuetify.breakpoint.mdAndUp"/>
      <br v-if="this.$vuetify.breakpoint.mdAndUp"/>
      <span :class="bodyClasses + ' font-weight-medium text--darken-3'">
        <span class="font-weight-bold text--darken-4">Capabilities: </span>
        {{capabilities}}
      </span>
      <br/><br/>
      <span :class="bodyClasses + ' font-weight-medium text--darken-3'">
        {{description}}
      </span>
    </span>
  </div>
</template>

<script>
export default {
  props: ['icon', 'title', 'capabilities', 'description'],
  data: () => ({
    tick: 0,
  }),
  computed: {
    accentSize() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return 85;
        case 'sm':
          return 120;
        case 'md':
          return 150;
        case 'lg':
          return 185;
        default:
          return 220;
      }
    },

    iconSize() {
      return `font-size: ${this.accentSize * 0.55}px;`;
    },

    borderSize() {
      return this.accentSize * 0.04;
    },

    barWidth() {
      return this.accentSize / 3;
    },

    changeDescClasses() {
      const baseClasses = 'font-weight-bold grey--text text--darken-4';

      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return `${baseClasses} body-1`;
        case 'sm':
          return `${baseClasses} title`;
        case 'md':
          return `${baseClasses} headline`;
        case 'lg':
          return `${baseClasses} display-1`;
        default:
          return `${baseClasses} display-1`;
      }
    },

    bodyClasses() {
      const baseClasses = 'grey--text';

      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return `${baseClasses} body-2`;
        case 'sm':
          return `${baseClasses} body-1`;
        case 'md':
          return `${baseClasses} title`;
        case 'lg':
          return `${baseClasses} headline`;
        default:
          return `${baseClasses} headline`;
      }
    },

    accentStyle() {
      const floatStyle = `float: left !important; width: ${this.accentSize + this.barWidth + this.borderSize}px; height: ${this.accentSize}px;`;
      return this.$vuetify.breakpoint.mdAndDown ? floatStyle : '';
    },

    containerStyle() {
      const cStyle = 'd-flex flex-direction-row align-start';
      let mStyle = '';

      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          mStyle = ' my-4';
          break;
        case 'sm':
          mStyle = ' my-8';
          break;
        case 'md':
          mStyle = ' my-12';
          break;
        case 'lg':
          mStyle = ' my-12';
          break;
        default:
          mStyle = ' my-12';
          break;
      }

      return this.$vuetify.breakpoint.mdAndDown ? mStyle : (cStyle + mStyle);
    },
  },
};
</script>

<style scoped>
div .action-icon-holder {
  border-radius: 50%;
  border-color: #FFE52D !important;
  border-style: solid;
}
</style>
